import { Button, CardHeader, CardTitle } from "react-bootstrap";
import { baseUrl } from "../../../../../../utils";

const SectionHeading = (props) => {

  const { title, section, cardTitleCss, icon, iconFull, selectedWallFeed, moreData, total, handlePopup, headingMargin, isDashboard,isTrial } = props

  // && selectedWallFeed && Object.keys(selectedWallFeed).length

  return (
    <CardHeader className={`${section==1 ? 'p-0 border-0' : section==2 ? 'p-3' : section==3 ? 'p-0 pb-3' : 'px-3 pt-3 border-0 pb-0'}`}>
      <CardTitle className={cardTitleCss ? cardTitleCss : `mb-0 d-flex align-items-center justify-content-between ${isDashboard ? isDashboard : ""}`}>
        <h6 className={`card-label font-weight-bolder ${!headingMargin ? 'm-0' : 'm-0'} `}>
         { icon ? <img src={baseUrl(`dashboard/analytic/${icon}.svg`)}  height={"20px"} width={"20px"} /> : null }
         { iconFull ? <img src={iconFull}  height={"20px"} width={"20px"} />  : null }   {title}</h6>
         { !isTrial && ((total > 5) || moreData) ? ( <Button variant="link" className="p-0" onClick={() => handlePopup(true)} > See More </Button> ) : null
        }
      </CardTitle>
    </CardHeader>
  );
};

export default SectionHeading;
