
import React, { PureComponent, Suspense } from "react";
import TopCard from "../../layout/common/topCard"; 
import Tabheader from "../../layout/common/subheader/tabHeader";
import { Card, CardBody } from "react-bootstrap";
import { connect } from "react-redux";
import { reArrangeSeries } from "../../../../../../utils";
import ImageSkeleton from "../../layout/skeleton/imageSkeleton";
import SectionHeading from "../../layout/common/sectionHeading";

const PostOverview = React.lazy(() =>
  import("./components/postOverview")
);

const PerfomingPost = React.lazy(() =>
  import("./components/perfomingPost")
);

const AssetsTypeLineChart = React.lazy(() =>
  import("../../layout/common/graph/assetsTypeLineChart")
);

class Widget extends PureComponent {

  render() {
    const { get_top_reacted_posts, get_post_overview_loader, selected_feed_data, get_post_overview_data, get_performing_post_data, get_reacted_posts_loader, get_performing_post_loader, get_web_analytic_data, get_web_analytic_loader } = this.props.analyticsRecords;
    const contentAnalytics = get_web_analytic_data && Object.keys(get_web_analytic_data).length && reArrangeSeries(get_web_analytic_data,3,1);
    const isTrial = !selected_feed_data ||  this.props.analyticsStatus

    return (
       <>
          <Tabheader />
            <div className="row">
              <TopCard loader={get_web_analytic_loader} data={get_web_analytic_data} col={4} res={3}  />
            </div>  

             <div className="row">
                <div className="col-lg-8 col-12 mb-4">
                  <Card className="sw-card-custom p-3 card-shadow h-100">
                   <SectionHeading headingMargin={1} section={3}  title={'Engagement Analytics'} />
                    <CardBody className="p-0 mt-3">
                       {  contentAnalytics ?    
                         <Suspense fallback={<></>}>
                            <AssetsTypeLineChart key={get_web_analytic_loader} contentAnalyticsData={get_web_analytic_loader ? null : contentAnalytics} type={1}/> 
                         </Suspense> : null 
                         }
                    </CardBody>
                  </Card>
                </div>
                <Suspense fallback={<></>}>
                  <PostOverview get_post_overview_loader={get_post_overview_loader} get_post_overview_data={get_post_overview_data} />
                </Suspense>
              </div>
             
              <Suspense fallback={<></>}>
                 <PerfomingPost isTrial={isTrial} key={get_performing_post_loader} fetchApi={'getPrfomingPostMore'} type={1} title={'Top Perfoming Posts'} loader={get_performing_post_loader} {...get_performing_post_data} />
              </Suspense>  


               <Suspense fallback={<></>}>
                 <PerfomingPost isTrial={isTrial}  selectedWallFeed={this.props.selectedWallFeed} key={get_reacted_posts_loader} fetchApi={'getreactedpostsMore'} type={2} title={'Top Reacted Posts'} loader={get_reacted_posts_loader} {...get_top_reacted_posts} />
               </Suspense>  
      </>
    );
  }
}

const displayStateprops = (state) => { 
  return {
    analyticsRecords: state.analyticsRecords,
    selectedWallFeed: state.analyticwall?.selectedWallFeed?.feeds,
    analyticsStatus: state.analyticsStatus.trialAccessAPI,
  };
};

export default connect(displayStateprops, '')(Widget);
