import { Link, useLocation } from "react-router-dom";

const Tabheader = () => {

  const active = useLocation()

  return (
    <div className="w-100">
      <ul className="tab-header-list w-100 d-flex align-items-center mb-3 p-0">
        <li>
          <Link to={"/analytic/activityHub/widget"} className={`d-block font-weight-boldest font-size-h7 text-dark px-2 pb-2 ${active.pathname.includes('widget') ? 'active' : ''}`}> Web </Link>
        </li>
        <li className="ms-5">
          <Link to={"/analytic/activityHub/display"} className={`d-block font-weight-boldest font-size-h7 text-secondary px-2 pb-2 ${active.pathname.includes('display') ? 'active' : ''}`}> Screen Analytics </Link>
        </li>
      </ul>
    </div>
  );
}

export default Tabheader
